.fluentform-admin-notice {
  display: block;
}

.fluentform-admin-notice .ff_logo_holder {
  max-width: 90px;
  display: inline-block;
  margin-right: 20px;
}

.fluentform-admin-notice .ff_logo_holder img {
  max-width: 100%;
}

.fluentform-admin-notice .ff_notice_container {
  display: inline-block;
  vertical-align: top;
  position: relative;
  padding-right: 40px;
  max-width: 67%;
}

.fluentform-admin-notice .ff_notice_container > h3 {
  margin: 0px;
}

.fluentform-admin-notice .ff_notice_container .ff_temp_hide_nag {
  position: absolute;
  right: 5px;
  top: 50%;
}

.fluentform-admin-notice .ff_notice_container .ff_temp_hide_nag .nag_cross_btn {
  cursor: pointer;
}

.fluentform-admin-notice .ff_notice_container .ff_temp_hide_nag .nag_cross_btn:hover {
  color: gray;
}




